<template lang="html">
  <div class="login">
<van-nav-bar
  title=""
  left-text="" right-text=""
  left-arrow
  @click-left="$router.go(-1)" @click-right="$router.push({path:'/login'})"
/>
    <section>

<van-cell-group>
  <van-field
    v-model="account"
    label=":"
    placeholder="" required
  />
  <van-field
    center
    v-model="sms"
    label=""
    placeholder=""
    icon="clear"
    @click-icon="sms = ''" required
  >
    <ge-code slot="button" :config=config class="van-button van-button--primary van-button--small"></ge-code>
  </van-field>
  <van-field
    v-model="password"
    type="password"
    label=""
    placeholder=""
    required
  />

</van-cell-group>
    </section>
    
    <van-button size="large" style="width:90vw;margin-left:5vw;" @click="doReg"> </van-button>




    <v-baseline></v-baseline>
    <v-footer></v-footer>
  </div>
</template>

<script>

import Baseline from '@/common/_baseline.vue'
import Footer from '@/common/_footer.vue'
import { Toast } from 'vant'
import geCode from 'vue-gecode'
import Vant from 'vant';
import 'vant/lib/vant-css/index.css';
import Vue from 'vue';
import md5 from 'js-md5';
Vue.use(Vant);

export default {
  components:{
    'v-baseline': Baseline,
    'v-footer': Footer,
    geCode
    
  },
  data(){
    return {
      account:'',
      password:'',sms:'',
      smsBtnTxt:'',
      toggle:!this.$store.state.login.token,
      config: {
        //
        startText: '',
        //
        endText: '',
        //()
        totalTime: 30,
        //()
        tickTime: 1,
        //class  isRun
        activeClass: 'isRun',
        //
        computeText(num) {
            //num 
            return ' ' + num + 's';
        },
        // 
        canTodo: () => {
            //
            let result = /^1[345678]\d{9}$/.test(this.account);
            console.log('',result);
            if(!result){
                alert('');
            }
            return result;
        },
        //canTode 
        todo: () => {
              let obj= {'api_time_sys':new Date().getTime(),'mobilenum':this.account};
              this.$dopost('/sysapi/user/mobile_code/', obj,
              function (res) {
              if(res.data && typeof(res.data.error) != "undefined" && res.data.error===0)
              {
                  if(res.data.data.state && res.data.data.state=='ok')
                  {
                      Toast('');
                  }
                  else
                  {Toast('');}
              }
              else{Toast((res.data && res.data.mess)?res.data.mess:'');}
              }.bind(this),true);
        },
        //
        endCallback(type, config) {
            //type   0:    this.$refs.geCode.stop(1)
            //config config  
            console.log('', type, config);
    
        },
        //
        click(isRun) {
            //isRun 
            console.log(`:${isRun ? '' : ''}`);

        },
        //   ture
        canUse: true,
        //  no-can
        noCanClass: 'no-can',
        }
      
    }
  },
  mounted()
  {
    if(this.$store.state.login.token==1)
    {
      this.$router.replace({path: '/user'});
    }
  },
  methods:{
    doReg(){
      if(this.account && this.account!=="" && this.password && this.password!=="") {

	  	this.$dopost('/sysapi/user/reg/', {'login_name':this.account,'login_pass':this.password,'mobile_code':this.sms},
		function (response) {
			if(response.data && typeof(response.data.error) != "undefined" && response.data.error===0)
			{
				this.toggle = false;
        this.$store.commit('CHANGE_USER',[1,response.data.user_id,md5(this.password)]);
        this.$router.replace({path: '/user'});
			}
			else{Toast((response.data && response.data.mess)?response.data.mess:'!');}
}.bind(this));

      }else {
        Toast('');
      }
/** 
      setTimeout(()=>{
        this.$router.replace({
          path: '/user'
        })
      },100);
**/
    },

    //
    sendMess(){
      Toast('sendMess');
      

    }
  }
}

</script>

<style lang="less" scoped>
.login {
  >section {
    margin-top: 10px;
    .tip {
      padding: 6vw 3vw;
      color:rgb(224, 145, 71);
      letter-spacing: 2px;
      font-size: 16px;
    }
  }
}

</style>


